.invoice-button {
  padding: 8px 14px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.invoice-button.orange {
  background-color: #FE940C;
  color: #ffffff;
}

.post-type-archive-invoice {
  background-color: #F7F8FA;
}

#invoices {
  margin: 8vh auto;
  max-width: 1200px;
  color: #6C7481;
  font-size: 16px;
}

.title-invoices--main {
  margin: 0 0 2vh;
  font-size: 2.5rem;
  font-weight: bolder;
}

.invoice-nav {
  display: flex;
  justify-content: space-between;
  margin: 0 -1rem;
  padding: 3vh 0;
}
.invoice-nav > * {
  padding: 0 1rem;
}
.invoice-nav .invoice-button {
  height: 100%;
}

#invoice-status {
  width: 30%;
}
#invoice-status .toggler-status {
  display: inline-block;
  padding: 4px 8px;
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 5px;
}
#invoice-status .toggler-status:not(:last-child) {
  margin-right: 5px;
}
#invoice-status .toggler-status.active {
  background-color: #6C7481;
  color: #ffffff;
}

#invoice-date-piceker {
  width: 30%;
}

.invoice-date-piceker__block {
  display: flex;
  justify-content: space-between;
  background-color: #ffffff;
  border: 1px solid #6C7481;
  border-radius: 5px;
  overflow: hidden;
}
.invoice-date-piceker__block .invoices-label {
  display: inline-block;
  width: 20%;
  background-color: #F2F3F5;
  padding: 4px 8px;
}
.invoice-date-piceker__block input {
  padding: 4px 8px;
  width: 40%;
  border: none;
}
.invoice-date-piceker__block input:focus-visible, .invoice-date-piceker__block input:focus {
  outline: none;
  border: none;
  background-color: #F7F8FA;
}
.invoice-date-piceker__block #invoice-date-piceker--submit {
  width: 35px;
  background-color: #FE940C;
  border: none;
  cursor: pointer;
  background-image: url(../../assets/check.png);
  background-size: 12px;
  background-position: center;
  background-repeat: no-repeat;
}

#invoice-search {
  display: flex;
  width: 20%;
  position: relative;
}
#invoice-search--field {
  padding: 4px 30px 4px 8px;
  width: 100%;
  border: none;
  border: 1px solid #6C7481;
  border-radius: 5px;
  background-color: #ffffff;
}
#invoice-search--field:focus-visible, #invoice-search--field:focus {
  outline: none;
  background-color: #ffffff;
}
#invoice-search--submit {
  position: absolute;
  top: 0;
  right: 1rem;
  width: 32px;
  height: 100%;
  border: none;
  z-index: 1;
  background-color: transparent;
  background-image: url(../../assets/search.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  cursor: pointer;
}

.invoice-table {
  background-color: #ffffff;
  border: 1px solid #6C7481;
  background-color: #ffffff;
  border-radius: 5px;
}
.invoice-table__labels {
  display: flex;
  margin: 0;
  padding: 10px 0;
  list-style: none;
  border-bottom: 1px solid #6C7481;
  font-weight: bolder;
}
.invoice-table .check {
  width: 5%;
}
.invoice-table .restaurant {
  width: 25%;
}
.invoice-table .id,
.invoice-table .status,
.invoice-table .start-date,
.invoice-table .end-date,
.invoice-table .total,
.invoice-table .fees,
.invoice-table .orders {
  width: 10%;
}
.invoice-table__result {
  margin: 0;
  padding: 0;
  position: relative;
  height: 410px;
  overflow: auto;
  color: #000000;
}
.invoice-table__result.loading::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 50px;
  background-image: url(../../assets/loading.svg);
  background-position: center;
  background-size: 50px;
  background-repeat: no-repeat;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.invoice-table__result li {
  display: flex;
  align-items: center;
  padding: 5px 0;
  border-bottom: 1px solid #F2F3F5;
}
.invoice-table__result li.nothing-found {
  padding: 5px 10px;
}
.invoice-table__result .check {
  position: relative;
  cursor: pointer;
}
.invoice-table__result .check::before {
  content: "";
  display: block;
  position: relative;
  margin: auto;
  width: 14px;
  height: 14px;
  border: 1px solid #6C7481;
  border-radius: 5px;
}
.invoice-table__result .check.active::before {
  background-image: url(../../assets/check.png);
  background-size: 8px;
  background-position: center;
  background-repeat: no-repeat;
}
.invoice-table__result .status span {
  display: block;
  width: 60px;
  padding: 3px 5px;
  text-align: center;
  font-size: 12px;
  text-transform: uppercase;
  border-radius: 15px;
}
.invoice-table__result .status span.pending {
  background-color: #E6E7EB;
}
.invoice-table__result .status span.verified {
  background-color: #4CBD8F;
  color: #ffffff;
}
.invoice-table__result .status span.ongoing {
  background-color: #FE940C;
  color: #ffffff;
}
.invoice-table__result .restaurant a {
  text-decoration: none;
  font-weight: bolder;
  display: flex;
  align-items: center;
}
.invoice-table__result .restaurant a img {
  margin-right: 5px;
  max-width: 30px;
  max-height: 30px;
  border-radius: 5px;
}
.invoice-table #pagination {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  border-top: 1px solid #F2F3F5;
  align-items: center;
  min-height: 50px;
}
.invoice-table #pagination__nav {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  border: 1px solid #F2F3F5;
}
.invoice-table #pagination__nav li:not(:last-child) {
  border-right: 1px solid #F2F3F5;
}
.invoice-table #pagination__nav li a {
  display: block;
  padding: 5px 10px;
  text-decoration: none;
}
.invoice-table #pagination__nav li a.active {
  background-color: #F2F3F5;
}

